<template>
    <div class="jurCurrent">
        <el-table class="web-table-jurCurrent" :data="jurData" style="width: 80%;margin: 0 auto;" min-height="250">
            <el-table-column prop="vehicleIdentificationNumber" label="车辆(vin码)"></el-table-column>
            <el-table-column prop="vehicleCertificateCode" label="流水号"></el-table-column>
            <el-table-column prop="creationDate" label="时间"></el-table-column>
            <el-table-column prop="processNode" label="状态">
                <template slot-scope="scope">
                    <router-link :to="{name: 'jurCurrentState', params: { objId: scope.row.id } }" class="flowOp">{{ scope.row.processNode }}</router-link>      
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import {getPretrialExamineListInfoFind} from '@/api/user'
import moment from 'moment'
export default {
    name: 'JurCurrent',
    data() {
        return {
            emptyUrl: require('../assets/empty.png'),
            jurData: []
        }
    },
    mounted () {
        getPretrialExamineListInfoFind({pretrialExamine: 0, size: 10, page: 1}).then(resp => {
            for(let i = 0, len = resp.data.content.length; i < len; i++) {
                resp.data.content[i].creationDate = moment(resp.data.content[i].creationDate).format('YYYY-MM-DD HH:mm:ss')
            }
            this.jurData = this.jurData.concat(resp.data.content)
            console.log(this.jurData)
        })
    },
    watch: {
        jurData: {
            handler(val){
                return val
            },
            immediate: true
        }
    },
    methods: {
        deleteRow(index, jurData) {
            this.$router.push({ path: 'jurCurrentState', query: jurData[index] });
        },
        moment: moment
    }
}
</script>

<style lang="scss" scoped>
.flowOp {
    color: #2f8ade;
    position: absolute;
    top: 7px;
    right: 12px;
    display: block;
    padding: 2px 10px;
    -webkit-box-shadow: 0 1px 6px #ccc;
    box-shadow: 0 2px 3px #ccc;
    border-radius: 4px;
    font-size: 12px;
    width: 84px;
    text-align: center;
}
.empty {
      text-align: center;
      margin-top: 42px;
      .text{
        color: #bbb;
        font-size: 14px;
      }
    }
</style>